<template>

</template>

<script>


export default {
  name: 'LoginPage',
  data() {
    return {
    }
  },
  created() {
      let type = this.GetUrlParam("type");
      if(type){
          this.$router.push('map-total')
      }else{
          this.$router.push('manage-water')
      }
  },
  methods: {
       GetUrlParam(paraName) {
           let url = document.location.toString();
           let arrObj = url.split("?");
           if (arrObj.length > 1) {
                let arrPara = arrObj[1].split("&");
                let arr;
                for (let i = 0; i < arrPara.length; i++) {
                    arr = arrPara[i].split("=");

                    if (arr != null && arr[0] == paraName) {
                        return arr[1];
                    }
                }
                return "";
           } else {
                return "";
           }
}
  }
}
</script>
